<template>
  <div class="block">
    <el-date-picker
      style="width:200px"
      class="a"
      v-model="value2"
      format="yyyy-MM-dd"
      value-format="yyyy-MM-dd"
      @change="getSTime"
      type="daterange"
      align="right"
      unlink-panels
      range-separator=""
      start-placeholder=""
      end-placeholder=""
      size="mini"
      :picker-options="pickerOptions">
    </el-date-picker>
    <br/>
    <br/>
    <el-button  v-on:click="postdate"></el-button>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        value1: '',
        value2: '',
      };
    },
    methods: {
      getSTime(val){
        this.value2=val;
      },
      //vuexstate()
      postdate(){
        console.log(this.value2)
        this.$store.commit('setDate',this.value2);
        alert("");
      }
    }
  };
</script>
